.aboutMain {
    background-color: rgb(19, 20, 24);
    color: white;
    padding: 10% 0%;
    margin: 0;

    display: flex;
    justify-content: space-evenly;

    box-shadow: inset 0px -61px 31px -53px rgba(0,0,0,1),inset 0px 61px 31px -53px rgba(0,0,0,01);    
}

.headshot {
    height: 500px;
    width: auto;
}

.aboutIntro {
    width: 40%;
    font-size: 170%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
}