.contactPage {
    background-color: white;
    padding: 13% 0%;
    color: white;

    display: flex;
    justify-content: center;
}

.contactPageCentering {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: rgb(38, 47, 85);
    width: 50%;
    padding: 5% 10%;
    box-shadow: 13px 19px 19px -2px rgba(0,0,0,0.9);
    border-radius: 90px;
}

.cpTitle {
    font-size: 300%;
    padding-bottom: 10%;
    text-align: center;
}

.cpMain {
    font-size: 180%;
    display: flex;
    justify-content: space-between;
}

.cpColumn1 {
    width: 50%;
    display: flex;
    flex-direction: column;
    text-align: right;
    padding-right: 4%;
}

.cpColumn2 {
    width: 50%;
    display: flex;
    flex-direction: column;
    text-align: left;
    padding-left: 4%;
}