.navbar {
    background-color: rgb(19, 20, 24);
    color:rgb(5, 5, 5);

    padding: 30px 30px;
    box-shadow: 0px -99px 14px -91px rgba(0,0,0,0.49) inset;
    -webkit-box-shadow: 0px -99px 14px -91px rgba(0,0,0,0.49) inset;
    -moz-box-shadow: 0px -99px 14px -91px rgba(0,0,0,0.49) inset;
    
}

.allButtons {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    color: white;
}

.leftButtons {
    display: flex;
    flex-wrap: nowrap;
    justify-content: start;
}

.rightButtons {
    display: flex;
    flex-wrap: nowrap;
    justify-content: end;
}
