.project {
    background-color: rgb(248, 248, 248);
    font-size: 50%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 10%;
    padding-bottom: 5%;
}

.projectTitle {
    margin-top: 10%;
    margin-bottom: 10px;
    font-size: 80%;
}

.projDescription {
    text-align: justify;
    width: 70%;
}

.projImage {
    max-width: 50%;
    height: auto;
}

.imageHolder {
    display: flex;
    justify-content: right;
}